.swiper-container {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px; }

.swiper-slide a {
  position: absolute;
  height: 100%; }

.swiper-slide a span {
  margin: 2px;
  position: absolute;
  bottom: 0;
  left: 20%;
  height: 20%;
  background-color: rgba(10, 10, 10, 0.5);
  color: #fff;
  display: flex;
  align-items: center; }

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px; }

.pic {
  width: 100%;
  height: 100%;
  object-fit: contain; }
