.find {
  height: 18px;
  width: 18px;
  position: absolute;
  top: 14px;
  right: 15px;
  cursor: pointer; }

.find:before {
  border: 2px solid #000;
  border-radius: 50%;
  box-sizing: border-box;
  left: 0;
  top: -7px;
  height: 14px;
  width: 14px;
  border-color: #ccc;
  content: '';
  display: block;
  position: absolute; }

.find:after {
  background: #ccc;
  bottom: 5px;
  transform: rotate(-45deg);
  right: 2px;
  height: 9px;
  width: 2px;
  content: '';
  display: block;
  position: absolute; }

